.lightbox,
  
  .image-card {
    width: 30vw;
    height: 30vw;
    object-fit: cover;
  }
  
  #lightbox-img {
    max-width: 80vw;
    object-fit: cover;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
    border-radius: 15px;

  }
  
  #lightbox {
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.5);
  }
  #view {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 70%;
  }
  
  
  
  
  
  
  /*Completely optional styling that has nothing to do with the lightbox*/
  button {
    color: white;
    border: 2px solid #a167da;
    border-radius: 50%;
    background-color: #a167da;
    font-size: 1.2rem;
  }
  
  img:hover, button:hover {
    cursor: pointer;
  }
  

  
  